import { useState, useEffect } from 'react';
import { registerBlockType } from '@wordpress/blocks';
import { RichText, useBlockProps } from '@wordpress/block-editor'
import { __ } from '@wordpress/i18n'
import block from './block.json';

registerBlockType(block.name, {
    edit({attributes, setAttributes}) {
        const { contentTitle, contentDescription, phpContent } = attributes;
        const blockProps = useBlockProps();

        useEffect(() => {
            const indexPath = '/wp-content/plugins/wpr-blocks/src/index.php';

            fetch(indexPath)
                .then(response => response.text())
                .then(data => setAttributes({ phpContent: data }))
                .catch(error => console.error('Error:', error));
        }, []);

        return (
            <div {...blockProps}>
                <RichText
                    tagName='h2'
                    placeholder={__('Add here the title', 'crb')}
                    value={contentTitle}
                    onChange={newVal => setAttributes({ contentTitle: newVal })}
                />
                <RichText
                    tagName='p'
                    placeholder={__('Add here the description', 'crb')}
                    value={contentDescription}
                    onChange={newVal => setAttributes({ contentDescription: newVal })}
                />

                <div className="fpcategories-content">
                    <h3>PHP content</h3>
                    <div dangerouslySetInnerHTML={{ __html: phpContent }} />
                </div>

            </div>
        );
    },
    save({ attributes }) {
        const { contentTitle, contentDescription, phpContent } = attributes;
        const blockProps = useBlockProps.save();
    
        return (
            <div {...blockProps}>
                <RichText.Content tagName='h2' value={contentTitle} />
                <RichText.Content tagName='p' value={contentDescription} />

                <div className="fpcategories-content">
                    <h3>What is the current year ?</h3>
                    <div dangerouslySetInnerHTML={{ __html: phpContent }} />
                </div>
            </div>
        );
    }
})